import React, { Component , useEffect} from 'react';
import photo2 from '../img/photo2.svg'
import './Overview.css';
import AOS from 'aos';
import 'aos/dist/aos.css';
const Overview =() =>{
  
 
    useEffect(()=>{
      AOS.init({  // values from 0 to 3000, with step 50ms
  duration: 3000,
   // values from 0 to 3000, with step 50ms
   // default easing for AOS animations
  
  
  })
  AOS.refresh();
  
    }, []);
    return (
      
       

<div >


  <section class="about-us" >
    <div class="about-us-img" data-aos="fade-right">
      <img src={photo2} alt="" />
    </div>

    <div class="about-us-text" data-aos="fade-up">
      <h2>Overview</h2>

      <p> The Senior Graduation Project highlights an important milestone for the Bachelor's and Students of graduate projects on their journey to learn about their scientific testimony. The graduation project A requirement that students in all colleges must provide. The Graduation Project Management System (GPMS) will certainly perform these educational tasks</p>

    </div>
  </section>
    
</div>
 );
}


export  default Overview;