import React from "react";
import {  MDBContainer, MDBRow, MDBCol, MDBIcon, MDBBtn, MDBInput } from "mdbreact";
import Contact from '../img/contact.svg';
import '../Contactus/Contactus.css';
const ContactPage = () => {
  return (
    <div className="container" style={{width:"100%"}}>
    <div style={{textAlign:"center",fontFamily: 'Vidaloka',   textTransform: "uppercase"}}>
      <h2>Contact Us</h2>
      
    </div>
    <div class="row" data-aos="fade-up">
      <div class="column">
        <img src={Contact} style={{width:"100%"}}/>
      </div>
      <div class="column">
        <form action="/x">
          <label for="fname">Email</label>
          <input type="text" id="email" name="Email" placeholder="Your Email.."/>
          <label for="lname">Name</label>
          <input type="text" id="name" name="Name" placeholder="Your Name.."/>
       
        <label for="subject">Message content </label>
          <textarea id="subject" name="subject" placeholder="Write Your message ." style={{height:"170px"}}></textarea>
          <input type="submit" value="Submit" />
        </form>
      </div>
    </div>
  </div> 
  );
}

export default ContactPage;