// src/components/bootstrap-carousel.component.js
import React from "react";

import { Carousel } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import Slide1 from "../img/startup-593327_1920.jpg";
import Slide2 from "../img/student-849825_1920.jpg";
import Slide3 from "../img/work-731198_1920.jpg";
import Slide4 from "../img/computer-1245714_1920.jpg";

class BootstrapCarouselComponent extends React.Component {

    render() {
        return (
            <div>
                <div className='container-fluid' autoPlay interval="0.2" transitionTime="1">
                    <div className="row">
                        <div className="col-sm-12">
                            
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-15">
                            <Carousel>

                                <Carousel.Item>
                                    <img
                                        className="d-block w-100"
                                        src={Slide1}
                                        alt="First slide"
                                       style={{
                                        
                                       }}
                                        
                                    />
                                    
                                </Carousel.Item>

                                <Carousel.Item>
                                    <img
                                        className="d-block w-100"
                                        src={Slide2}
                                        alt="Second slide"
                                        style={{
                                          
                                         }}
                                    />

                                   
                                </Carousel.Item>

                                <Carousel.Item>
                                    <img
                                        className="d-block w-100"
                                        src={Slide3}
                                        alt="Third slide"
                                        style={{
                                         
                                         }}
                                    />
                                  
                                </Carousel.Item>
                                <Carousel.Item>
                                    <img
                                        className="d-block w-100"
                                        src={Slide4}
                                        alt="Third slide"
                                        style={{
                                          
                                         }}
                                    />
                                  
                                </Carousel.Item>

                            </Carousel>
                        </div>
                    </div>
                </div>
            </div>
        )
    };
}

export default BootstrapCarouselComponent;