

import React, { Component } from 'react';
import {Link} from 'react-router-dom';

 export class Header extends Component {
     state={
         toggle:false,
         headerShow: false
     }
     menuToggle=()=>{

         this.setState({toggle: !this.state.toggle})
     }
     
    handleScroll = () => {
        if(window.scrollY > 0){
             this.setState({
                 headerShow: true
             })
        } else {
             this.setState({
                 headerShow: false
             })
        }
     }
 
    render() {
        const {toggle}=this.state;
        
        return (
            <div className="stiky" onMouseDown={this.handleScroll}>
                <header >
                    <div className="logo">
                       <h1>GPMS</h1>
                       <i class="fas fa-graduation-cap" />
                    </div>
                    
                    <ul className={toggle ? "toggle" :""} > 
                        <li><Link  to="/Home">Home</Link></li>
                        <li><Link  to="/Overview">Overview</Link></li>
                        <li><Link  to="/Projects">projects</Link></li>
                        <li><Link  to ="/About">About us</Link></li>
                        <li><Link  to="/Contact">Contact us</Link></li>
                        <li className="sign"><Link  to="/SignIn" target="_blank"  >Sing in </Link></li>
                        <li className="close" onClick={this.menuToggle} >X</li>
                    </ul>
                    <div className="menu" onClick={this.menuToggle}>Menu</div>

               </header>
            </div>
        )
    }
}

export default Header;